<template>
  <div class="msg-history">
    <Button class="btn-back" type="warning" @click="back">返回</Button>
    <Button class="btn-clear" @click="clearList">清空列表</Button>
    <Table class="history-table" stripe :columns="historyHeader" :data="historyData"></Table>
    <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
    <div class="tag-modal index-modal" v-if="indexModal">
      <h3>请输入要删除的消息的索引</h3><br>
      <span style="color: red;font-size:12px;">Tips:&nbsp;&nbsp;&nbsp;0或者不填为全部删除<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1、2、3...为对应第几条</span>
      <br>
      <br>
      <Input type="text" v-model="article_idx" placeholder="您要删除第几条消息？" />
      <Button class="btn" type="warning" @click="indexModalClose">取消</Button>
      <Button class="btn" type="success" @click="indexModalSubmit">确定</Button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      indexModal: false,
      msg_id: '',
      article_idx: '',
      dataList: [],
      tagList: [],
      historyData: [],
      historyHeader: [
        {
          key: "media_id",
          title: "media_id",
          align: "center",
          width: 100
        },
        {
          title: "发送对象",
          align: "center",
          width: 100,
          render: (h, params) => {
            let tagId = params.row.tagId;
            let sex;
            let arr = this.tagList
            for(let i = 0; i < arr.length; i ++) {
              if(arr[i].id == tagId) {
                sex = arr[i].name
              }
            }
            return h("span", {
              props: {}
            }, sex)
          }
        },
        {
          key: "update_time",
          title: "发送消息时间",
          align: "center",
          render: (h, params) => {
            let time = this.formatDate(params.row.update_time)
            return h('span', {
              props: {}
            }, time)
          }
        },
        {
          title: "是否成功",
          align: "center",
          width: 200,
          render: (h, params) => {
            let msg_id = params.row.msg_id;
            let success;
            if(msg_id == "") {
              success = "无msg_id,发送不成功"
            } else {
              success = "msg_id存在,发送成功"
            }
            return h("span", {
              props: {}
            }, success)
          }
        },
        {
          title: "图文标题",
          align: "center",
          width: 300,
          render: (h, params) => {
            let arr = params.row.content.news_item.length != 0 ? params.row.content.news_item : [];
            let titles = [];
            for(let i = 0; i < arr.length; i ++) {
              titles.push(i + 1 + '.' + arr[i].title + "\r")
            }
            return h(
              "p",
              {
                style: {
                  textAlign: 'center',
                  padding: '5px'
                }
              },
              titles
            );
          }
        },
        {
          key: "timing",
          title: "定时时间",
          align: "center",
          render: (h, params) => {
            let time = "~~"
            if(params.row.timing) {
              time = this.formatDate(params.row.timing/1000)
            }
            return h(
              "span",
              {
                props: {}
              },
              time
            );
          }
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "warning",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.indexModal = true;
                      that.msg_id = params.row.msg_id
                    }
                  }
                },
                "删除消息"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
    this.getTagList();
  },
  methods: {
    back() {
      this.$router.push({
        name: "群发列表"
      });
    },
    clearList() {
      this.$axios.get('/history/clear', {params: {code: this.$route.query.code}})
      .then(res => {
        this.$Message.info(res.data.success)
        this.showDataList()
      })
    },
    getTagList() {
      this.$axios.get("/material/tag", {params: {code: this.$route.query.code}}).then(res => {
        this.tagList = res.data.data;
      });
    },
    showDataList() {
      this.$axios
        .get("/history", { params: { code: this.$route.query.code } })
        .then(res => {
          this.dataList = res.data.data;
          this.historyData = this.dataList.slice(0, 10)
        });
    },
    indexModalClose() {
      this.indexModal = false;
      this.article_idx = ""
    },
    indexModalSubmit() {
      this.$axios.get('/history/del_msg', {
        params: {
          code: this.$route.query.code,
          msg_id: this.msg_id,
          article_idx: this.article_idx
        }
      }).then(res => {
        this.$Message.info(res.data.success);
        this.indexModal = false;
        this.article_idx = ""
      })
    },
    formatDate(time) {
      var date = new Date(Number(time)*1000);
      var year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        min = date.getMinutes(),
        sec = date.getSeconds();
      var newTime =
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min) +
        ":" +
        (sec < 10 ? "0" + sec : sec);
      return newTime;
    },
    changePage(index){
      var _start = ( index - 1 ) * 10;
      var _end = index * 10;
      this.historyData = this.dataList.slice(_start,_end);
      this.goTop()
    },
    goTop () {
      let self = this
      self.timer = setInterval(function () {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let ispeed = Math.floor(-osTop)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        self.isTop = true
        if (osTop === 0) {
          clearInterval(self.timer)
        }
      }, 30)
    }
  }
};
</script>

<style scoped>
.history-table {
  width: 100%;
  clear: both;
  margin-bottom: 30px;
}
.tag-modal {
  width: 300px;
  /* height: 180px; */
  padding: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -165px;
  margin-top: -105px;
  background: #eee;
  z-index: 100000;
  color: #999;
  text-align: left;
}
.tag-modal .btn {
  margin-top: 30px;
  margin-right: 20px;
}

.btn-back {
  margin-bottom: 30px;
  float: left;
}
.btn-clear {
  float: right;
}
</style>

